import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { select, Store } from '@ngrx/store';
import { filter, finalize, debounceTime, distinctUntilChanged, switchMap, take } from 'rxjs/operators';
import { combineLatest, of } from 'rxjs';
import { ValidationService } from '../shared/validators/validators.service';
import { AppState, getAuthUser, getCompany } from '../store/reducers';
import { TargetService } from '../api/target.service';
import * as moment from 'moment';

import { BaseComponent } from '../shared/helpers/base.component';
import { ConfirmationModalService } from '../shared/confirmation-modal/confirmation-modal.service';
import { ConfirmationData } from '../shared/confirmation-modal/confirmation.interface';
import { SegmentService } from '../segment/segment.service';
import { AnalyticsSegment } from '../segment/analytics-segment';
import { Company, StoreCompany } from '../types/company';
import { isCompanyPreviewRole, IUser } from '../models';
import {
  ModalResponse,
  SendIndividualRewardModalComponent
} from '../shared/send-individual-reward-modal/send-individual-reward-modal.component';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { RewardsService } from '../api/rewards.service';
import { ToastrService } from 'ngx-toastr';
import { Member, Target } from '../models/target';

@Component({
  selector: 'upb-target',
  templateUrl: './target.component.html',
  styleUrls: ['./target.component.scss']
})
export class TargetComponent extends BaseComponent implements OnInit {
  form: FormGroup;
  maxTarget = 80;
  loading = false;
  company: StoreCompany;
  user: IUser;
  selectedCompany: Company;
  bsModalRef: BsModalRef;
  ncsrValue: number;
  targetData: Target;
  targetMembers: Member[];
  showSuccessAlert = false;
  isAuthorizeChecked = true;
  isFeeChecked = true;
  bsValue: string | Date = new Date();
  minDate: string | Date = new Date();
  probableExpirationDate: any;
  totalPrice: any;
  public toast: any;
  public showError: boolean;
  public currency: string;

  constructor(
    private fb: FormBuilder,
    private store: Store<AppState>,
    private target: TargetService,
    private validationService: ValidationService,
    private confirmationService: ConfirmationModalService,
    private modal: BsModalService,
    private toasterService: ToastrService,
    private rewardsService: RewardsService,
    private segment: SegmentService
  ) {
    super();
    this.createForm();
  }

  ngOnInit(): void {
    this.subs = this.form.controls.target.valueChanges
      .pipe(
        distinctUntilChanged(),
        debounceTime(500),
        switchMap(() => {
          if (this.form.controls.target.errors && this.form.controls.target.errors.max) {
            const confirmationTemplate: ConfirmationData = this.createConfirmTemplate();
            return this.confirmationService.showConfirmation(confirmationTemplate, 'Back to Edit');
          } else {
            return of('valid');
          }
        }),
        filter(val => val !== 'valid')
      )
      .subscribe(() => {
        if (this.confirmationService.getActionType()) {
          const actionType: string = this.confirmationService.getActionType();
          if (actionType === 'askMore') {
            this.segment.showChat();
          } else {
            this.form.controls.target.setValue('80');
          }
        }
      });

    this.subs = combineLatest(this.store.pipe(select(getCompany)), this.store.pipe(select(getAuthUser)))
      .pipe(filter(([company, user]) => Boolean(company && user)))
      .subscribe(([company, user]) => {
        this.company = company;
        this.selectedCompany = company.selectedCompany;
        this.currency = this.selectedCompany.currency ? this.selectedCompany.currency : 'usd';
        this.user = user;
        const ncsr = company.newClientSchedulingRate.shortValue;
        this.ncsrValue = isNaN(+ncsr) ? 0 : ncsr;
        this.getTarget();
      });
  }

  public newReward(): void {
    this.bsModalRef = this.modal.show(SendIndividualRewardModalComponent, {
      class: 'modal-lg',
      backdrop: 'static'
    });
    this.bsModalRef.content.members = this.company.members;
    this.bsModalRef.content.currency = this.selectedCompany.currency ? this.selectedCompany.currency : 'usd';
    this.subs = this.modal.onHidden.pipe(take(1)).subscribe(payload => {
      const rewardData: ModalResponse = JSON.parse(payload);
      if (rewardData && this.selectedCompany) {
        if (rewardData.amount < 0 && this.user.role !== 'admin' && this.user.role !== 'support') {
          this.toasterService.error('Reward amount must be greater than zero.');
        } else {
          this.rewardsService
            .sendOneOffReward({
              companyId: this.company.selectedCompany.id,
              ...rewardData
            })
            .subscribe({
              complete: () => {
                this.toasterService.success('Reward successfully sent.');
              },
              error: error => {
                this.toasterService.error(error.message);
              }
            });
        }
      }
    });
  }

  private createForm(): void {
    this.form = this.fb.group({
      target: ['', [Validators.required, ValidationService.nonNegative]],
      daysDuration: ['', Validators.required],
      rewardSum: ['', [Validators.required, ValidationService.nonNegative]],
      rewardPartialSum: ['', [Validators.required, ValidationService.nonNegative]],
      rewardTertiarySum: [''],
      renewable: [''],
      startDate: ['', Validators.required]
    });
  }

  private getTarget(): void {
    this.subs = this.target.getTarget(this.company.selectedCompany.id).subscribe(target => {
      this.targetData = target.data;
      this.targetMembers = target.data.members;
      if (this.targetData.id) {
        let checkDate: boolean = false;
        if (target.data.lastTimeChecked) {
          checkDate = moment(target.data.lastTimeChecked).isAfter(target.data.startDate);
        }
        const startDateShow =
          target.data.lastTimeChecked && checkDate ? target.data.lastTimeChecked : target.data.startDate;

        let targetSum: number = null;
        if (!this.targetData.target) {
          targetSum = this.ncsrValue && this.ncsrValue < 78 ? this.ncsrValue + 3 : this.ncsrValue;
        } else {
          targetSum = this.targetData.target;
        }

        if (this.user.role === 'admin') {
          this.maxTarget = 100;
        } else {
          this.maxTarget = targetSum > 80 ? targetSum : this.maxTarget;
        }
        this.form.setValue(
          {
            target: targetSum,
            daysDuration: target.data.daysDuration.toString(),
            rewardSum: target.data.rewardSum,
            rewardPartialSum: target.data.rewardPartialSum,
            rewardTertiarySum: target.data.rewardTertiarySum || '',
            renewable: target.data.renewable,
            startDate: moment(moment(startDateShow)).format('MMMM Do YYYY')
          },
          { emitEvent: false }
        );

        this.form.controls.target.setValidators([Validators.max(this.maxTarget), ValidationService.nonNegative]);
        this.targetData.expirationDate = moment(moment(target.data.expirationDate)).format('MMMM Do YYYY');
      } else {
        this.targetMembers.map(member => {
          member.reward = true;
          member.workType = 'full';
        });
        this.form.controls.daysDuration.setValue('14');
        this.form.controls.renewable.setValue(true);

        if (this.ncsrValue && this.ncsrValue < 78) {
          this.form.controls.target.setValue(this.ncsrValue + 3);
        }

        this.maxTarget = this.user.role === 'admin' ? 100 : this.maxTarget;
        this.form.controls.target.setValidators([Validators.max(this.maxTarget), ValidationService.nonNegative]);
      }
      this.calculateTotalPrice();
      if (isCompanyPreviewRole(this.user)) {
        const startDate = this.targetData && this.targetData.startDate ? this.targetData.startDate : this.minDate;
        this.minDate = moment(startDate)
          .clone()
          .subtract(1, 'month')
          .toDate();
      }
    });
  }

  public onDateChange(event: any): void {
    let startDateFormat = {};
    if (event && this.form.get('daysDuration').value) {
      startDateFormat = moment(event);
      this.targetData.startDate = event;
    }
    if (!event && this.form.get('daysDuration').value) {
      startDateFormat = moment(moment(this.targetData.startDate)).format('YYYY-MM-DDTHH:mm:ss.SSSSZ');
    }
    this.probableExpirationDate = moment(startDateFormat)
      .add(Number(this.form.get('daysDuration').value), 'd')
      .format('MMMM Do YYYY');
  }

  public getPriceChanged(): void {
    this.calculateTotalPrice();
  }

  private calculateTotalPrice(): void {
    const fullMemberReward = this.form.get('rewardSum').value;
    const partialMemberReward = this.form.get('rewardPartialSum').value;
    const rewardTertiarySum = this.form.get('rewardTertiarySum').value;
    const numFullTimeMembers = this.targetMembers.filter(member => member.reward && member.workType === 'full').length;
    const numPartialTimeMembers = this.targetMembers.filter(member => member.reward && member.workType === 'partial')
      .length;
    const numBackOfficeMembers = this.targetMembers.filter(member => member.reward && member.workType === 'tertiary')
      .length;

    this.totalPrice =
      numFullTimeMembers * fullMemberReward +
      numPartialTimeMembers * partialMemberReward +
      numBackOfficeMembers * rewardTertiarySum;
  }

  private createConfirmTemplate(): ConfirmationData {
    return {
      title: `Setting a realistic target!`,
      message:
        'Howdy!<br><br>' +
        'Setting your Reward to ' +
        this.form.controls.target.value +
        '%?<br><br>' +
        'UPbook data shows that 80% should be the max target - as 80% is the perfect' +
        ' balance between high new clients and high team morale.<br><br>' +
        'If you would still like to set your Reward above 80%, ' +
        'just ask us below and we&#39;ll get this sorted for you!',
      buttonsSet: [
        {
          name: 'Ask to set 95%',
          actionType: 'askMore',
          className: 'btn-action'
        },
        {
          name: 'Set 80% target and continue',
          actionType: 'setMax',
          className: 'btn-primary'
        }
      ]
    };
  }

  public confirm(): void {
    if (!this.form.valid) {
      if (this.form.controls.target.touched && this.form.controls.target.errors) {
        const confirmationTemplate: ConfirmationData = this.createConfirmTemplate();
        this.subs = this.confirmationService.showConfirmation(confirmationTemplate, 'Back to Edit').subscribe(() => {
          if (this.confirmationService.getActionType()) {
            const actionType: string = this.confirmationService.getActionType();
            if (actionType === 'askMore') {
              this.segment.showChat();
            } else {
              this.form.controls.target.setValue('80');
            }
          }
        });
        return;
      }
      this.validationService.markAllControlsAsTouched(this.form);
      return;
    }
    this.showError = false;
    const formPayload = this.form.value;
    if (formPayload.renewable === '') {
      formPayload.renewable = true;
    }
    // fix issue with wrong "Days Left" in new-client-scheduling-rate.component
    formPayload.startDate = moment(this.targetData.startDate).startOf('day');
    formPayload.daysDuration = Number(formPayload.daysDuration);
    formPayload.members = {
      ...this.targetMembers
    };
    formPayload.active = true;
    if (this.targetData && this.targetData.id) {
      this.subs = this.target
        .updateTarget(this.targetData.id, formPayload)
        .pipe(finalize(() => (this.loading = false)))
        .subscribe(
          target => {
            this.showSuccessAlert = true;
            this.targetData = target.data;
            this.segment.track(AnalyticsSegment.RewardsActivated);
          },
          error => {
            const message: string = error.error
              ? error.error
              : 'Something goes wrong. Please reload page and try again.';
            this.showError = true;
            this.toast = {
              message: message,
              type: 'danger'
            };
          }
        );
    } else {
      this.subs = this.target
        .saveTarget(this.company.selectedCompany.id, formPayload)
        .pipe(finalize(() => (this.loading = false)))
        .subscribe(
          target => {
            this.showSuccessAlert = true;
            this.targetData = target.data;
            this.segment.track(AnalyticsSegment.RewardsActivated);
          },
          error => {
            const message: string = error.error
              ? error.error
              : 'Something goes wrong. Please reload page and try again.';
            this.showError = true;
            this.toast = {
              message: message,
              type: 'danger'
            };
          }
        );
    }
  }

  getRewardValue(rewardMember: Member) {
    switch (rewardMember.workType) {
      case 'full':
        return this.form.get('rewardSum').value;
      case 'tertiary':
        return this.form.get('rewardTertiarySum').value;
      default:
        return this.form.get('rewardPartialSum').value;
    }
  }

  isTertiaryRewardDisabled(member: Member) {
    return !this.form.get('rewardTertiarySum').value && member.workType !== 'tertiary';
  }

  formatMoney(value: any): string {
    return value ? '$' + value : '$0';
  }

  preventClose(event: MouseEvent) {
    event.stopImmediatePropagation();
  }
}
